.footer {
  background-color: #191919;
  color: #ffffff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__logo {}
  &__madeby {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    font-size: 12px;
    img {
      margin-right: 8px;
      margin-left: 8px;
    }
  }
  &__call {
    text-align: center;
    display: flex;
    flex-direction: column;
    line-height: 1.2;
    span {
      font-size: 13px;
      font-weight: 400;
      margin-bottom: 10px;
    }
  }
}