.header {
  background-color: #191919;
  color: #ffffff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
    position: relative;
  }
  &__logo {}
  &__work-time {
    font-size: 1.2rem;
    line-height: 24px;
    max-width: 85px;
    text-align: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  &__call {
    display: flex;
    align-items: center;
  }
  &__phones {
    display: flex;
    flex-direction: column;
    line-height: 1.2;
  }
  &__call-btn {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    padding: 20px 28px;
    background-color: #1669b3;
    border: none;
    margin-left: 28px;
  }
}