html {
  font-size: 10px;
  font-family: 'Proxima Nova', sans-serif;
  font-weight: 400;
}
body {
  font-size: 1.8rem;
}

.container {
  // max-width: 1200px;
  width: 100%;
  margin: auto;
  padding: 0 15px;
}

.section-header {
  text-align: center;
  margin: auto;
  &__title {
    font-size: 3.6rem;
    font-weight: 900;
    margin-bottom: 30px;
  }
  &__subtitle {
    font-size: 2rem;
    font-weight: 400;
  }
}

input[type="text"],
input[type="email"],
input[type="tel"] {
  width: 100%;
  font-size: 1.3rem;
  text-transform: uppercase;
  background-color: #f2f2f2;
  border: none;
  padding: 30px;
  padding-right: 60px;
  &::placeholder {
    color: #818181;
  }
  &:focus::placeholder {
    opacity: 0;
  }
}

.tel {
  font-weight: 900;
}

.swiper-button-prev {
  &:before,
  &:after {
    content: '';
    position: absolute;
    background-color: #fff;
    width: 15px;
    height: 4px;
  }
  &:after {
    left: 10px;
    transform-origin: left top;
    transform: rotate(45deg);
  }
  &:before {
    left: 10px;
    transform-origin: left bottom;
    transform: rotate(-45deg);
  }
}
.swiper-button-next {
  &:before,
  &:after {
    content: '';
    position: absolute;
    background-color: #fff;
    width: 15px;
    height: 4px;
  }
  &:after {
    right: 10px;
    transform-origin: right bottom;
    transform: rotate(45deg);
  }
  &:before {
    right: 10px;
    transform-origin: right top;
    transform: rotate(-45deg);
  }
}