@charset "utf-8";

@import 'basic/basic';
@import 'blocks/header';
@import 'blocks/footer';

////variables
$color-accent: #f7b516;
$color-accent2: #ffbc3b;
$color-dark: #08243e;
$color-half-dark: #0b3554;
$bg1: url(../img/bg/bg1.jpg);
$bg2: url(../img/bg/bg2.jpg);
$bg3: url(../img/bg/bg3.jpg);
$bg4: url(../img/bg/bg4.jpg);
$bg5: url(../img/bg/bg5.jpg);
// $fscreenIcon1: url(../img/icon/first_screen/icon1.png);
// $fscreenIcon2: url(../img/icon/first_screen/icon2.png);
// $fscreenIcon3: url(../img/icon/first_screen/icon3.png);
// $fscreenIcon4: url(../img/icon/first_screen/icon4.png);
////=========


.first-screen {
  min-height: 100vh;
  background: no-repeat center / cover;
  background-image: $bg1;
  position: relative;
  &__content {
    background-color: $color-accent;
    color: #000000;
    width: 50%;
    padding: 100px;
  }
  &__title {
    font-size: 4rem;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
  &__subtitle {
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 100px;
  }
  &__features {

  }
  &__feature {
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    padding-right: 60px;
  }
  &__icon {
    margin-right: 25px;
  }
}

.get-price {
  background: no-repeat center / cover;
  background-image: $bg2;
  &__inner {
    display: flex;
    justify-content: flex-end;
    padding-top: 100px;
    padding-bottom: 100px;
  }
  &__title {
    font-size: 3rem;
    font-weight: 900;
    color: #000000;
    text-align: right;
    margin-bottom: 60px;
  }
  &__form {
    max-width: 770px;
    width: 100%;
  }
}
.get-price-form {
  & > form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__input,
  &__btn,
  &__text {
    flex-basis: 48%;
  }
  &__input {
    position: relative;
    margin-bottom: 30px;
    &::after {
      display: block;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
    }
    &:nth-child(1)::after {
      content: url(../img/icon/mail.png);
    }
    &:nth-child(2)::after {
      content: url(../img/icon/phone.png);
    }
    & > input {
      // width: 100%;
      // font-size: 1.3rem;
      // text-transform: uppercase;
      // background-color: #f2f2f2;
      // border: none;
      // padding: 30px;
      // padding-right: 60px;
    }
  }
  &__btn {
    order: 4;
    input {
      width: 100%;
      padding: 30px;
      background-color: #1866af;
      font-size: 1.7rem;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
      border: none;
    }
  }
  &__text {
    text-align: right;
    font-size: 1.4rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: 700;
    padding-right: 60px;
    position: relative;
    background: no-repeat right center;
    background-image: url(../img/icon/clock.png);
  }
}

.features {
  background-color: $color-dark;
  padding: 100px 0;
  &__header {
    color: #ffffff;
    max-width: 430px;
    margin-bottom: 100px;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 35px;
  }
  &__item {
    flex-basis: calc(25% - 33.5px);
  }
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  &__img {
    margin-bottom: 50px;
  }
  &__text {
    min-height: 50px;
    text-align: center;
    color: #ffffff;
  }
}

.find-detail {
  padding: 100px 0;
  background: no-repeat center / cover;
  background-image: $bg3;
  &__header {
    color: #ffffff;
    margin-bottom: 60px;
    max-width: 620px;
  }
  &__form {
    margin-bottom: 60px;
    & > form {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
  &__input,
  &__btn {
    flex-basis: 31%;
  }
  &__input {
    position: relative;
    &::after {
      display: block;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
    }
    &:nth-child(2)::after {
      content: url(../img/icon/phone.png);
    }
  }
  &__btn {
    & > input {
      font-size: 1.7rem;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
      background-color: $color-accent;
      border: none;
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1.4rem;
    color: #ffffff;
    & > span:nth-child(1) {
      margin-bottom: 30px;
    }
    & > span:nth-child(2) {
      margin-bottom: 15px;
    }
    & > .tel {
      font-size: 3rem;
      font-weight: 800;
    }
  }
}

.warranty {
  padding: 100px 0;
  &__header {
    max-width: 690px;
    margin-bottom: 100px;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 70px;
    padding-right: 70px;
  }
  &__item {
    flex-basis: 30%;
  }
}

.warranty-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img {
    margin-bottom: 40px;
  }
  &__text {
    text-align: center;
    min-height: 50px;
    margin-bottom: 30px;
  }
  &__more {
    // font-size: 18px;
    font-weight: 700;
    text-decoration: underline;
    text-transform: uppercase;
    color: #000000;
  }
}

.bonus {
  background-color: $color-dark;
  padding: 100px 0;
  &__header {
    color: #ffffff;
    max-width: 680px;
    margin-bottom: 100px;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    flex-basis: calc(25% - 33px);
  }
}

.bonus-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  &__img {
    width: 100px;
    height: 100px;
    border: 2px solid #ffffff;
    border-radius: 50%;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    & > span {
      font-size: 3rem;
      font-weight: 700;
      color: #ffffff;
    }
  }
  &__text {
    text-align: center;
    color: #ffffff;
  }
}

.questions {
  background-color: $color-half-dark;
  padding: 100px 0;
  &__header {
    color: #ffffff;
    max-width: 450px;
    margin-bottom: 100px;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #ffffff;
  }
  &__list-wrapper {
    flex-basis: 48%;
  }
  &__more {
    font-size: 1.4rem;
    text-decoration: underline;
    &::before {
      content: url(../img/icon/reload.png);
      vertical-align: middle;
      margin-right: 20px;
    }
    &:hover {
      color: #579eff;
      text-decoration: none;
    }
  }
  &__photo {
    flex-basis: 48%;
  }
}

.questions-list {
  color: #ffffff;
  list-style: decimal;
  margin-left: 20px;
  &__item {
    padding-left: 30px;
    font-size: 1.6rem;
    font-weight: 700;
    margin-bottom: 55px;
    position: relative;
    & > a {
      text-decoration: underline;
      &:hover,
      &:focus {
        color: #579eff;
        text-decoration: none;
      }
    }
  }
  &__tooltip {
    visibility: hidden;
    background-color: #ffffff;
    padding: 22px 50px 22px 22px;
    position: absolute;
    z-index: 5;
    top: 50px;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
    font-size: 1.4rem;
    font-weight: 400;
    color: #000000;
    max-width: 330px;
    &--active {
      visibility: visible;
    }
    &::before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      background-color: #fff;
      transform: rotate(45deg);
      position: absolute;
      top: -10px;
      left: 30px;
    }
    & > button {
      width: 20px;
      position: absolute;
      right: 20px;
      top: 20px;
      background: none;
      border: none;
      cursor: pointer;
      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #15151e;
      }
      &::before {
        transform: rotate(45deg);
      }
      &::after {
        transform: rotate(-45deg);
      }
    }
  }
}


